* {
  font-family: "Factor A", sans-serif;
  font-weight: 400;
  font-style: normal;
}
html {
  overflow-x: hidden 
}
body {
  padding-right: 0 !important;
  color: #000;
}

html .--fw700 {
  font-weight: 700;
}
html .--fw500 {
  font-weight: 500;
}
.--uppercase {
  text-transform: uppercase;
}

.page-btn {
  @include page-btn();
}
.page-link {
  @include page-link();
}
.page-h2 {
  @include page-h2();
}
.page-text-20 {
  @include page-text-20();
}
.page-text-14 {
  @include page-text-14();
}


[data-accordion="content"] {
  max-height: 0;
  -webkit-transition: max-height .3s;
  -o-transition: max-height .3s;
  transition: max-height .3s;
  overflow: hidden;
}
[data-tabs="element"] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity .3s ease-in,visibility .3s ease-in;
  -o-transition: opacity .3s ease-in,visibility .3s ease-in;
  transition: opacity .3s ease-in,visibility .3s ease-in;  
}
[data-tabs="element"].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.title {
  position: relative;
  .title__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  .title__wrapper {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: end;
  }
  .title__main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 89px;
  }
  .title__info {
    
  }
  .title__text {

  }
  .title__btn {

  }
  .title__bottom {

  }
}

.programms {
  padding-top: 150px;
  padding-bottom: 75px;
  .programms__wrapper {

  }
  .programms__header {

  }
  .programms__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 10px;
  }
  .programms__item {
    color: #fff;
    position: relative;
    padding: 25px;
    border-radius: 10px;
    overflow: hidden;
    min-height: 391px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .programms__item-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    object-fit: cover;
  }
  .programms__item-top {
    display: flex;
    justify-content: space-between;
    align-items: start;
  }
  .programms__item-type {

  }
  .programms__item-tags {

  }
  .programms__item-tag {
    display: block;
    padding: 4px 15px;
    border: 1px solid #fff;
    border-radius: 5px;

    &:not(:last-child) {
      margin-bottom: 5px;
    }
    &.--orange {
      background: #d64e34;
    }
  }
  .programms__item-body {

  }
  .programms__item-author {
    display: block;
    margin-bottom: 33px;
    span {
      display: block;
    }
  }
  .programms__item-date {
    color: #000;
    display: block;
    margin-bottom: 9px;
  }
  .programms__item-place {
    display: block;
  }

  .programms__items-link {
    margin-top: 30px;
  }

  .masters__item {

  }
  .masters__item-img {
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 20px;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .masters__item-name {
    display: block;
    margin-bottom: 5px;
  }
  .masters__item-post {
    display: block;
    color: #a6a7ab;
  }
}

.faq {
  padding: 75px 0;
 .faq__wrapper {

 } 
 .faq__header {
  margin-bottom: 50px;
 }
 .faq__items {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0 13px;
 }
 .faq__item {
  padding: 36px 0;
  border-bottom: 1px solid #ADADAD;
  border-top: 1px solid #ADADAD;

  
  &.is-active {
    .faq__btn {
      img {
        transform: rotate(-90deg);
      }
    }
  }
 }
 .faq__btn {
  display: flex;
  justify-content: space-between;
  align-items: center;

  img {
    transition: .3s;
  }
 }
 .faq__content {

 }
 .faq__content-inner {
  padding-top: 20px;
 }
}

