@mixin page-btn {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  border: none;
  box-shadow: none;

  padding: 18px 50px;
  border: 1px solid #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 110%;
  text-align: center;
  display: inline-block;
  color: #fff;

  &.--orange {
    background: #d64e34;
    border-color: #d64e34;
  }
}

@mixin page-h2 {
  font-size: 28px;
  font-weight: 700;
  line-height: 100%;
  text-transform: uppercase;
}
@mixin page-text-20 {
  font-size: 20px;
  font-weight: 400;
  line-height: 120%;
}
@mixin page-text-14 {
  font-size: 14px;
  font-weight: 400;
  line-height: 120%;
}
@mixin page-link {
  font-size: 16px;
  font-weight: 700;
  line-height: 120%;
  text-decoration: underline;
  display: flex;
  justify-content: start;
  align-items: center;
  
  img {
    margin-left: 4px;
  }
}