.breadcrumbs {
  padding: 125px 0 0;
  .breadcrumbs__wrapper {
    
  }
  .breadcrumbs-items {
    display: flex;
    justify-content: start;
    align-items: center;
  }
  .breadcrumbs-item {
    &:not(:last-child) {
      color: #e84e0f;
      margin-right: 3px;
      &::after {
        content: ' /';
        font: inherit;
        color: inherit;
      }
    }
  }
}
