.footer {
    color: #fff;
    background: #d64e34;
    padding-top: 150px;
    padding-bottom: 22px;
    a {
        color: inherit;
    }
   .footer__wrapper {

   }
   .footer__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
   }
   .footer__col {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
   }
   .footer__mail {
    font-size: 28px;
    font-weight: 700;
    line-height: 120%;
    text-align: left;
   }
   .footer__nav {
    margin: 100px 0;
   }
   .footer__link {
    display: block;
    font-size: 14px;
    font-weight: 700;
    line-height: 16.8px;
    text-align: left;
    &:not(:last-child) {
        margin-bottom: 15px;
    }
   }
   .footer__policy {
    font-size: 14px;
    font-weight: 700;
    line-height: 16.8px;
    opacity: .6;    
   }
   .footer__info {

   }
   .footer__info-header {
    font-size: 14px;
    font-weight: 700;
    line-height: 16.8px;
    opacity: .6;    
    display: block;
    margin-bottom: 14px;
   }
   .footer__info-val {
    margin: 0;
    font-size: 14px;
    font-weight: 700;
    line-height: 16.8px;

   }
   .footer__docs {

   }
   .footer__docs-text {
    font-size: 14px;
    font-weight: 700;
    line-height: 16.8px;
    opacity: .6;    
   }
   .footer__docs-nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 40px;
   }
   .footer__bottom {
    margin-top: 100px;
   }
}