@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Factor A";
  font-display: swap;
  src: url("../fonts/FactorA-Bold.ttf");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Factor A";
  font-display: swap;
  src: url("../fonts/FactorAMedium-Regular.ttf");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Factor A";
  font-display: swap;
  src: url("../fonts/FactorA-Regular.ttf");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Factor A";
  font-display: swap;
  src: url("../fonts/FactorALight-Regular.ttf");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1502px;
  margin: 0 auto;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 110px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1000;
}
.header .header__wrapper {
  padding: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
}
.header .header__left {
  display: flex;
  justify-content: start;
  align-items: center;
}
.header .header__burger {
  position: relative;
  z-index: 110;
  cursor: pointer;
}
.header .header__burger.is-active .header__burger-inner::before, .header .header__burger.is-active .header__burger-inner::after {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  bottom: unset;
}
.header .header__burger.is-active .header__burger-inner::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.header .header__burger.is-active .header__burger-item {
  opacity: 0;
}
.header .header__burger-btn {
  padding: 21px 15px;
  background: #d64e34;
  width: 53px;
  height: 54px;
  margin-right: 15px;
}
.header .header__burger-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100%;
}
.header .header__burger-inner::before {
  content: "";
  width: 100%;
  height: 1px;
  background: #fff;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.3s;
}
.header .header__burger-inner::after {
  content: "";
  width: 100%;
  height: 1px;
  background: #fff;
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
  transition: 0.3s;
}
.header .header__burger-item {
  width: 23px;
  height: 1px;
  background: #fff;
  transition: 0.3s;
}
.header .header__burger-list {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background: #d64e34;
  padding: 131px 50px;
  z-index: 100;
  max-width: 361px;
  width: 100%;
  transform: translateX(-150%);
  transition: 0.3s;
}
.header .header__burger-list.is-active {
  transform: translateX(0);
}
.header .header__burger-link {
  display: block;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
}
.header .header__burger-link:not(:last-child) {
  margin-bottom: 30px;
}
.header .header__socials {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-left: 15px;
  position: relative;
  z-index: 110;
}
.header .header__social {
  width: 42px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .header__social:not(:last-child) {
  margin-right: 15px;
}
.header .header__logo {
  display: none;
}

.main-nav__list {
  display: flex;
  margin: 0;
  margin-right: 20px;
  margin-left: 60px;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}

.footer {
  color: #fff;
  background: #d64e34;
  padding-top: 150px;
  padding-bottom: 22px;
}
.footer a {
  color: inherit;
}
.footer .footer__grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.footer .footer__col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer .footer__mail {
  font-size: 28px;
  font-weight: 700;
  line-height: 120%;
  text-align: left;
}
.footer .footer__nav {
  margin: 100px 0;
}
.footer .footer__link {
  display: block;
  font-size: 14px;
  font-weight: 700;
  line-height: 16.8px;
  text-align: left;
}
.footer .footer__link:not(:last-child) {
  margin-bottom: 15px;
}
.footer .footer__policy {
  font-size: 14px;
  font-weight: 700;
  line-height: 16.8px;
  opacity: 0.6;
}
.footer .footer__info-header {
  font-size: 14px;
  font-weight: 700;
  line-height: 16.8px;
  opacity: 0.6;
  display: block;
  margin-bottom: 14px;
}
.footer .footer__info-val {
  margin: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16.8px;
}
.footer .footer__docs-text {
  font-size: 14px;
  font-weight: 700;
  line-height: 16.8px;
  opacity: 0.6;
}
.footer .footer__docs-nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 40px;
}
.footer .footer__bottom {
  margin-top: 100px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.breadcrumbs {
  padding: 125px 0 0;
}
.breadcrumbs .breadcrumbs-items {
  display: flex;
  justify-content: start;
  align-items: center;
}
.breadcrumbs .breadcrumbs-item:not(:last-child) {
  color: #e84e0f;
  margin-right: 3px;
}
.breadcrumbs .breadcrumbs-item:not(:last-child)::after {
  content: " /";
  font: inherit;
  color: inherit;
}

