* {
  font-family: "Factor A", sans-serif;
  font-weight: 400;
  font-style: normal;
}

html {
  overflow-x: hidden;
}

body {
  padding-right: 0 !important;
  color: #000;
}

html .--fw700 {
  font-weight: 700;
}

html .--fw500 {
  font-weight: 500;
}

.--uppercase {
  text-transform: uppercase;
}

.page-btn {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  border: none;
  box-shadow: none;
  padding: 18px 50px;
  border: 1px solid #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 110%;
  text-align: center;
  display: inline-block;
  color: #fff;
}
.page-btn.--orange {
  background: #d64e34;
  border-color: #d64e34;
}

.page-link {
  font-size: 16px;
  font-weight: 700;
  line-height: 120%;
  text-decoration: underline;
  display: flex;
  justify-content: start;
  align-items: center;
}
.page-link img {
  margin-left: 4px;
}

.page-h2 {
  font-size: 28px;
  font-weight: 700;
  line-height: 100%;
  text-transform: uppercase;
}

.page-text-20 {
  font-size: 20px;
  font-weight: 400;
  line-height: 120%;
}

.page-text-14 {
  font-size: 14px;
  font-weight: 400;
  line-height: 120%;
}

[data-accordion=content] {
  max-height: 0;
  -webkit-transition: max-height 0.3s;
  -o-transition: max-height 0.3s;
  transition: max-height 0.3s;
  overflow: hidden;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
  -o-transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.title {
  position: relative;
}
.title .title__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.title .title__wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: end;
}
.title .title__main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 89px;
}
.programms {
  padding-top: 150px;
  padding-bottom: 75px;
}
.programms .programms__items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}
.programms .programms__item {
  color: #fff;
  position: relative;
  padding: 25px;
  border-radius: 10px;
  overflow: hidden;
  min-height: 391px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.programms .programms__item-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
}
.programms .programms__item-top {
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.programms .programms__item-tag {
  display: block;
  padding: 4px 15px;
  border: 1px solid #fff;
  border-radius: 5px;
}
.programms .programms__item-tag:not(:last-child) {
  margin-bottom: 5px;
}
.programms .programms__item-tag.--orange {
  background: #d64e34;
}
.programms .programms__item-author {
  display: block;
  margin-bottom: 33px;
}
.programms .programms__item-author span {
