.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 1000;
    .header__wrapper {
        padding: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        position: relative;
    }
    .header__left {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .header__burger {
        position: relative;
        z-index: 110;
        cursor: pointer;

        &.is-active {
            & .header__burger-inner::before,
            & .header__burger-inner::after {
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(45deg);
                bottom: unset;
            }
            & .header__burger-inner::after {
                transform: translate(-50%, -50%) rotate(-45deg);
            }
            .header__burger-item {
                opacity: 0;
            }
        }
    }
    .header__burger-btn {
        padding: 21px 15px;
        background: #d64e34;
        width: 53px;
        height: 54px;
        margin-right: 15px;
    }
    .header__burger-inner {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        height: 100%;

        &::before {
            content: '';
            width: 100%;
            height: 1px;
            background: #fff;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            transition: .3s;
        }
        &::after {
            content: '';
            width: 100%;
            height: 1px;
            background: #fff;
            display: block;
            position: absolute;
            bottom: -1px;
            left: 0;
            transition: .3s;
        }
    }
    .header__burger-item {
        width: 23px;
        height: 1px;
        background: #fff;
        transition: .3s;
    }

    .header__burger-list {
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        background: #d64e34;
        padding: 131px 50px;
        z-index: 100;
        max-width: 361px;
        width: 100%;
        transform: translateX(-150%);
        transition: .3s;

        &.is-active {
            transform: translateX(0);
        }
    }
    .header__burger-list__inner {
        
    }
    .header__burger-link {
        display: block;
        font-size: 14px;
        font-weight: 700;
        line-height: 14px;
        text-decoration: none;
        text-transform: uppercase;
        color: #fff;

        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }
    .header__socials {
        display: flex;
        justify-content: start;
        align-items: center;
        margin-left: 15px;
        position: relative;
        z-index: 110;
    }
    .header__social {
        width: 42px;
        height: 42px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:not(:last-child) {
            margin-right: 15px;
        }
    }
    .header__right {

    }
    .header__btn {

    }
    .header__logo {
        display: none;
    }
}
